.masa-israel {
  box-sizing: border-box;

  padding-top: 87px;

  height: 46.7vw;

  background: url("../img/bg-masa-desktop@1x.png") 5px 0 no-repeat;
  background-size: 99.9% auto;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-masa-desktop@2x.png");
    background-size: 1361px auto;
  }
}

.masa-israel__heading {
  @include heading-h2;

  margin-left: auto;
  margin-right: 470px;
  margin-bottom: 0;

  width: 100px;
}

.masa-israel__text {
  margin-top: 23px;
  margin-left: auto;
  margin-right: 176px;
  padding: 0;

  width: 390px;

  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;

  font-size: 17px;
  line-height: 29px;

  color: $white;

  opacity: 0.87;

  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
}

.masa-israel__button {
  @include button-white-text;
  @include button-yellow-background;

  position: relative;

  box-sizing: border-box;

  display: block;

  margin-left: auto;
  margin-right: 340px;
  margin-top: 48px;
  padding-top: 21px;
  padding-left: 31px;

  width: 230px;
  height: 59px;

  text-decoration: none;
  text-align: center;

  border-radius: 80px;

  &:hover {
    @include button-yellow-background-hover;
  }

  &:active {
    outline: none;

    @include button-yellow-background-active;
  }
}

.masa-israel__button::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 40px;

  width: 24px;
  height: 24px;

  background: url("../img/icon-in-white.svg") 0 0 no-repeat;
}

@media (max-width: 1023px) {
  .masa-israel__heading {
    width: 50%;
    text-align: center;

    margin-left: auto;
    margin-right: auto;
    padding: 0;
  }

  .masa-israel__text {
    box-sizing: border-box;

    margin: 0 auto;
    margin-top: 20px;
    padding: 0;

    width: 70%;

    font-size: 15px;
    line-height: 27px;

    text-align: center;
  }

  .masa-israel__button {
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;

    width: 230px;
    height: 59px;
  }
}

@media (max-width: 767px) {
  .masa-israel {
    margin-top: 74px;
    padding-top: 0;
    padding-bottom: 0;

    min-height: 123.8vw;

    background: url("../img/bg-masa-mobile@1x.png") 0 45px no-repeat;
    background-size: 99.9% auto;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-masa-mobile@1x.png");
    }
  }

  .masa-israel__text {
    box-sizing: border-box;

    display: block;

    width: 85%;

    margin-top: 30vw;
    padding-left: 23px;
    margin-left: 0;

    font-style: normal;
    font-weight: normal;

    font-size: 16px;
    line-height: 26px;

    color: $white;

    opacity: 0.87;

    text-align: left;

    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
  }

  .masa-israel__button {
    position: relative;

    margin: 0 auto;
    margin-top: 57px;

    width: 82.5vw;
  }

  .masa-israel__heading {
    margin: 0;
    padding: 0;

    width: 100%;
  }

  .masa-israel__button::before {
    top: 18px;
    left: 58px;
  }
}
